@import url(https://fonts.googleapis.com/css?family=Barlow:400,500,600,700|Barlow+Semi+Condensed:400,600,700&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fbfaf9;
}

.Stage {
  width: 100%;
  margin-bottom: 1.5rem;
}

.Stage h3 {
  font-size: 1.2rem;
  padding: 9px;
}

.Stage.ChartStage, .Stage.ProfileStage {
  min-height: 480px;
}

.Stage.TableStage div.card-body {
  padding: 0;
}

.Stage .axis-title {
  color: #444;
  padding-right: 1rem;
  font-size: 0.9em;
  font-family: 'Barlow', 'Barlow Semi Condensed', 'Roboto';
}

.Stage form {
  margin-bottom: 0.4em;
  margin-top: 0.2em;
}

.Legend {
  position: absolute;
  right: 0;
  bottom: 1em;
  margin-right: 1.5em;
  margin-bottom: 1.7em;
  border: 1px solid #ddd;
  z-index: 1000;
  padding: 0.5em;
  font-size: 1.0rem;
  font-family: 'Barlow Semi Condensed';
  background-color: #ffffffee;
}

.Legend div.vx-legend-shape > div {
  border: 1px solid #888;
}

.Chart {
  font-family: 'Barlow Semi Condensed';
  min-height: 400px;
  min-width: 300px;
}


.Chart div.responsive-container {
  /* min-height: 400px !important; */
}

.Chart.Choropleth {
  /* min-width: 300px; */
  width: 100%;

}


.Chart .leaflet-container {
  width: 100%;
  min-height: 500px;
  margin: 0 auto;
}

.Chart div.leaflet-control-layers.leaflet-control {
	visibility: hidden;
}

.Chart .custom-tip {
  background-color: #333;
  color: white;
  border-color: #333;
  opacity: 0.85;
  font-size: 0.95em;
  font-family: 'Barlow Semi Condensed';
  padding: 3px;
  font-weight: 600;
  border-radius: 2px;
  z-index: 500;
  pointer-events: none;
}

.Chart .custom-tip::before {
  border-top-color: #333;
}

.Chart .tooltip-content  {
  position: relative;
  /* padding: 4px; */
  -webkit-transform: translate(20%, -105%);
          transform: translate(20%, -105%);
  pointer-events: none;
}

.Chart .axis text {
  fill: #444;
}

.Chart .axis path {
  stroke: lightgrey;
}

.Chart .axis line.axis-baseline {
  stroke: #bbb;
  stroke-width: 1px;
}

.Chart .axis-label text {
  font-size: 0.9em;
}

.Chart .axis-title text {
  font-family: 'Barlow', 'Barlow Semi Condensed', 'Roboto';
}

.Chart g.direct-label text {
  font-size: 0.9em;
  font-weight: 700;
  text-anchor: end;
  /* fill: white; */
}

.Chart g.direct-label.light text {
  fill: white;
}

.Chart g.direct-label.dark text {
  fill: #303030;
}

.Chart .chart-annotation g.annotation-note-content {
  font-size: 0.9em;
  -webkit-transform: translate(0.2em, -0.6em);
          transform: translate(0.2em, -0.6em);
}

.Facet {
  margin: 4px 0;
}

.Facet .facet-heading {
  font-size: 1.0em;
  font-weight: 500;
  font-family: 'Barlow';
  margin-bottom: 2px;
}

.Facet g.frame-title {
  display: none;
}


.Chart .ChartHelper {
  float: right;
}

.Chart .ChartHelper button {
  border-radius: 0.5em;
}

#chart-help-popover {
  font-family: 'Barlow', 'Barlow Semi Condensed', 'Roboto';
}

.Profile {

}

.Profile table {
  font-size: 1.1rem;
  margin-bottom: 0.2em;
}


.Profile table thead {
  /* display: none; */
}

.Profile div.list-group-flush .list-group-item {
  padding: 0;
}

.Profile .infobox {
  padding: 0.5rem 0.75rem;
  color: #222;
  background-color: #fcfcff;
}

.Profile .infobox ul {
  margin-bottom: 0;
}

.Profile .infobox .small {
  font-size: 90%;
}

.Profile .infobox .title {
  font-weight: 500;
}

.Profile .row-state {
  background-color: #fcfcfc;
  font-style: italic;
}

.Profile tr.row-state td:nth-child(1) {
  padding-left: 1.2em;
}

.Footer {

}

.Footer ul li span.source {
  font-weight: 600;
}

.Footer img#logo {
  max-width: 150px;
  margin-top: 0.8em;
}

.Footer h3 {
  font-size: 1.1em;
}

.Footer .h4 {
  font-weight: 500;
}

.Footer hr {
  width: 50%;
}

.Footer .alert-light hr {
  border-top-color: #e7e4e2;
}

.VizContainer {
  min-height: 400px;
  min-width: 300px;
}


.VizContainer span.nav-icon {
  margin-right: 0.3em;
}

.VizContainer div.RRT__panel {
  border-width: 0;
  min-width: 300px;
}

.VizContainer div.RRT__tab {
  /* margin: -0.2em 0 0.5em 0; */
  color: #615b61;
  background-color: white;
  padding-top: 0.2em;
  padding-bottom: 0.3em;
  border-width: 0;
}

.VizContainer div.RRT__tab:hover {
  border-color: #ede6db;
  background-color: #F8F5F0;
}

.VizContainer div.RRT__tab--selected {
  /* color: #625c50; */
  color: #82317e;
  font-weight: 600;
  /* border-color: #625c50; */
  border-color: #82317e;
  border-width: 0;
  border-bottom-width: 3px;
  border-radius: 2px;
}

.App {
  font-size: 14px;
  font-family: 'Barlow', sans-serif;
  /* padding: 2em; */
  padding-bottom: 2em;
}

.App header h1 {
  /* padding: 0.8em 1.2em; */
  margin: 0.8em 0 1.2em 0;
}

.App header h2 {
  margin: 1em 0 0.5em 0;
}

.App h2, .App h3 {
  font-weight: 500;
}

.App a {
  color: #af1166;
  font-weight: 600;
}

.App hr {
  margin: 0.6em 2em 1em 2em;
}

